@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: 'Roboto Mono', monospace;;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 100vh;
  min-height: 600px;
  background-color: #f9f9f9;
}
h2 {
    margin: 0;
}

.game {
    margin: 0 auto;
    max-width: 288px;
    max-height: 600px;
    background-color: #ffffff;
    border-radius: 8px;
    padding: 30px 20px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, .3);
}

.cp-outer {
    border-bottom: 1px solid #cacaca;
    padding-bottom: 16px;
}

.cp-heading, .cp-control {
    display: flex;
    justify-content: space-between;
}

.cp-heading {
    margin-bottom: 20px;
}

.cp-logo {
    width: 24px;
}

.field {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 26px auto 0;
}

.cell {
    width: 30px;
    height: 30px;
    background-color: #1f1f1f;
    margin: 3px;
    border-radius: 2px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 22px;
    justify-content: center;
    align-items: center;
    display: flex;
    color: #753131;
    font-weight: bold;
}

    .one-bomb {
        color: #0304f5;
    }

    .two-bombs {
        color: #027204;
    }

    .three-bombs {
        color: #b00004;
    }

    .four-bombs {
        color: #010180;
    }

    .five-bombs {
        color: #730408;
    }

    .six-bombs {
        color: #228485;
    }

    .seven-bombs {
        color: #4a694a;
    }

    .eight-bombs {
        color: #3d3d94;
    }

.bomb {
    background-color: #be6157 !important;
    background-image: url(/Minesweeper_Hyperskill/static/media/logo.41c2f52f.svg);
    box-shadow: inset 0 2px 4px 0 rgba(0, 0, 0, .2);
}

.empty {
    background-color: #c4c4c4 !important;
    box-shadow: inset 0 2px 4px 0 rgba(0, 0, 0, .2);
}

.flagged {
    background-image: url(/Minesweeper_Hyperskill/static/media/flag.d0c808b8.svg);
}

.reset-button {
    cursor: pointer;
    font-size: 16px;
    background-color: white;
    border: 0;
    border-radius: 4px;
    transition: 0.5s;
}

.reset-button:hover {
    background-color: #ececec;
}
